/*
Fixes: So that the calculation for the translate3d
in thumbnails will be correct.
react-responsive-carousel 3.2.11 did not include
margin, padding and border when calculating the
translate3d.
*/
.MyCarousel ul {
  #margin: 0;
  #padding: 0;
}

.MyCarousel .carousel .thumb {
  margin: 0;
  padding: 0;
  border: none;
}
/* End of fixes */

.MyCarousel {
  width: 400px;
  padding: 10px;
  border: 1px solid white;
}

.MyCarousel .carousel .slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

.MyCarousel .carousel .thumbs-wrapper {
  margin: 0;
  background-color: black;
}

.MyCarousel .carousel .thumb {
  vertical-align: middle;
  cursor: pointer;
  box-shadow: inset 0 0 0 3px #333;
}

.MyCarousel .carousel .thumb.selected,
.MyCarousel .carousel .thumb:hover {
  box-shadow: inset 0 0 0 3px white;
}

.MyCarousel .carousel .thumb img {
  padding: 10px;
}